class PriceTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            bucket: [],
            tableData: []
        };
    }

    componentDidMount() {
        fakeApi.then((data) => {
            data.forEach(x => x.isAdded = false);
            this.setState({tableData: data});
        });
    }

    handleProductAdded = (e) => {
        let {bucket, tableData} = this.state;
        const obj = bucket.find(x => x.id === e.id);
        if (!obj) {
            bucket.push(e);
            tableData.find(x => x.id === e.id).isAdded = true;
        } else {
            bucket = bucket.filter(x => x.id !== obj.id);
            this.setState({bucket, tableData})
        }
    }

    render() {
        const {bucket, tableData} = this.state;

        return (
            <div className="priceTable">
                <div className="priceTable-header">
                    <Header value={bucket}/>
                </div>
                <div className="priceTable-main">
                    <Sort/>
                    <Sale tableData={tableData} onItemAdded={this.handleProductAdded}/>
                    <News/>
                </div>
                <div className="priceTable-footer">
                    <Footer/>
                </div>
            </div>
        );
    }
}

// ========================================

ReactDOM.render(<PriceTable/>, document.getElementById('root'))



